import React from 'react';

import { faPlusCircle, faMinusCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';



class Products extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            verIconoMasoMenos: true,
            fondoMenu: true
        }
    }

    render() {
        return (
            <>
                <div className="container col-5" style={{ minWidth: "452px" }} >
                    <div className="container" style={{ marginBottom: "20px", paddingLeft: "10px" }}>
                        <div className="row">
                            <Link to={{ pathname: "/products/callinectes" }} >
                                <img src={this.props.iconoProducto} alt={this.props.tituloProducto} height="80px" width="80px" />
                            </Link>



                            <button className="custombtn "  type="button" data-toggle="collapse"
                                data-target={`#${this.props.idcollapse}`}
                                aria-expanded="false"
                                aria-pressed="false"
                                aria-controls={this.props.idcollapse}
                                onClick={() => this.setState({ verIconoMasoMenos: !this.state.verIconoMasoMenos })}

                                style={{ marginLeft: "5px", marginBottom: "10px", marginRight: "5px", marginTop: "5px", maxWidth: "325px", minWidth: "325px" }}>
                                <span >{this.props.tituloProducto}</span> 
                                <br />
                                {this.state.verIconoMasoMenos ?
                                    <span><FontAwesomeIcon icon={faPlusCircle} />   More</span> :
                                    <span><FontAwesomeIcon icon={faMinusCircle} /> Less</span>} 


                                <div className="collapse multi-collapse" id={this.props.idcollapse}>
                                    <div className="container p-0">

                                        <div >
                                            <span className="fromtext">{this.props.fromText}</span>

                                            <div className="btn-group">


                                                <div className="customrow" >
                                                    <p className="card-text text-justify pr-1 pl-1 w-50 border-3 mb-0" ><h6 className="text-center font-weight-bold ">FEATURES</h6>{this.props.featureText}</p>
                                                    <p className="card-text text-justify pr-1 pl-1 w-50"><h6 className="text-center font-weight-bold">BENEFITS</h6>{this.props.benefitText}</p>
                                                </div>
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </button>

                        </div>  



                    </div>

                </div>
            </>

        )
    }
}

export default Products;