import React from 'react';
import Callidown from '../../img/callidown.png';
import Calliup from '../../img/calliup.png';
import blueCrabIcon from '../../img/bluecrabicon.png';
import claws from '../../img/claws.png';
import cocktailClaw from '../../img/cocktailclaws.png';
import lump from '../../img/lump.png';
//import Footer from '../footer/Footer';
import jumboLump from '../../img/jumbolump.png';

class Callinectes extends React.Component {
    render() {
        return (
            <>
            <div style={{backgroundColor:"white", width:"100%", marginBottom:"50px",height: "688px"}}>

            
                <div className="container" style={{  backgroundColor: "rgba(255,255,255,0.5)" }}>
                    <section className="text-center">
                        <div className="container">
                            <h1 className="titulos">PRODUCTS </h1>

                        </div>
                    </section>
                    <section className="container" style={{  marginBottom: "10px" }}>
                        <div className="row">
                            <div className="col-3" style={{  margin: "3px" }}>

                                <div className="row">
                                    <img src={blueCrabIcon} alt="logo callinectes" height="50px" width="50px" />
                                    <h5 style={{ paddingTop: "10px" }}>CALLINECTES SAPIDUS</h5>
                                </div>

                                <br></br>
                                <h6><strong>Common Name:</strong> Blu Crab</h6>
                                <br />
                                <h6><strong>Origin:</strong> This species is most abundant from Massachusetts to Texas, the Gulf of Mexico and Mexico itself. Good supplies exist in South America,(mainly in Venezuela, Colombia and Ecuador).</h6>
                                <br />
                                <h6><strong>Description:</strong> Crustaceo</h6>
                            </div>
                            <div className="col text-center " style={{  margin: "3px" }}>
                                <div className="img" >
                                    <img src={Calliup} alt="Upside" width="100%" />
                                </div>
                                <div className="container" >
                                    <img src={Callidown} alt="Downside" width="112%" style={{paddingRight:"20px"}} />
                                </div>
                            </div>
                            <div className="col-5 text-center" style={{  margin: "3px" }}>
                                <div>
                                    <div className="row">
                                        <div className="parrafodetalle">
                                            <h5 className="text-left" style={{ paddingTop: "10px", borderBottom: "2px solid crimson" }}>CLAW</h5>
                                            <p className="pdetalle">
                                                Claw consist of brown meat from the firts section of the crab claw with part of the shell removed. Their stronger flavor profile makes the ideal for heavy sauces or dips.
                                            </p>
                                        </div>
                                       
                                        <img className="iconodetalle"  src={claws} alt="tenasas" />                                        
                                    </div>
                                </div>
                                
                                <div>
                                    <div className="row">
                                        <div className="parrafodetalle"> 
                                            <h5 className="text-left" style={{ paddingTop: "10px", borderBottom: "2px solid crimson" }}>COCKTAIL CLAW</h5>
                                            <p className="pdetalle">
                                                Is the last segment of the claw in which the shell has been completely removed, except for the tip, which may serve as a handle for dipping meat in cocktail sauce or melted  butter. 
                                            </p>
                                        </div>
                                       
                                        <img className="iconodetalle" src={cocktailClaw} alt="tenasas" />                                        
                                    </div>
                                </div>
                                
                                <div>
                                    <div className="row">
                                        <div className="parrafodetalle">
                                            <h5 className="text-left" style={{ paddingTop: "10px",borderBottom: "2px solid crimson" }}>LUMP</h5>
                                            <p className="pdetalle">
                                                Claw consist of brown meat from the firts section of the crab claw with part of the shell removed. Their stronger flavor profile makes the ideal for heavy sauces or dips.
                                            </p>
                                        </div>
                                       
                                        <img className="iconodetalle"  src={jumboLump} alt="tenasas"  />                                        
                                    </div>
                                </div>

                                <div>
                                    <div className="row">
                                        <div className="parrafodetalle">
                                            <h5 className="text-left" style={{ paddingTop: "10px", borderBottom: "2px solid crimson"}}>
                                                JUMBO LUMP
                                                </h5>
                                            <p className="pdetalle">
                                                Consisting on the two large muscles connected to the swimming fins of the crab, is prozed for its impressive size, bright white color and exquisite taste. It is best used in dishes where the size of the lumps can really shine.
                                            </p>
                                        </div>
                                       
                                        <img className="iconodetalle"  src={lump} alt="tenasas" />                                        
                                    </div>
                                </div>
                            </div>
                        </div>

                        

                    </section>

                </div>
                </div>
                
            </>
        )
    }
}

export default Callinectes;