import React from 'react';
import Cangrejo from '../../../img/cangrejoabout.png' ;
class Jumbotron extends React.Component {
 
  render() {
 
    return (
 
        <div className="aboutus">
          <div className="row">
            <div className="container col-12 text-center">
              <h1 className="titulos">ABOUT US </h1>
            </div>
            <div className="col-6"  >
              <img src={Cangrejo} alt="Cangrejo Azul" height="100%" width="100%" />
            </div>
            <div className="col-4">
               
                <p className="pabout text-justify">
                  We a specialized company buying sea food from procedure industries in Maracaibo - Venezuela using high standards to offer the best products through its distribution and commerce to big supermarkets and restaurants-related companies in all the United States of America.
                </p>
                <p className="pabout text-justify">
                We have a commitment to offer quality food through a genuine and a nice process selection for our sea food providers with the highest quality standards and the best distribution system that goes from the product exportation to its delivery to our potential clients, who are going to make the product reach the final user.
                </p>
                <p className="pabout text-justify">
                We have a qualified staff that is dedicated tocleave the clients satisfied through a customer service and competitive prices, focused in developing strong relationships with buyers based in trust and satisfaction.
                </p>
                
            </div>
          </div>
          
        </div>
 
    )
    
  }
 
}
 
export default Jumbotron;
