import React from 'react';
import Product from './product/Product';
import blueCrabIcon from '../../img/bluecrabicon.png';

class Products extends React.Component {
    render() {
        return (
            <><div style={{ backgroundColor: "white", width: "100%", marginBottom: "50px", height: "688px" }}>


                <div className="container" id="contenedorproductos">
                    <section className="text-center">
                        
                            <h1 className="titulos">Products </h1>

                        
                    </section>
                    <div className="mialbum" >
                        <div className="row">
                            <Product 
                            iconoProducto={blueCrabIcon}
                            tituloProducto="CALLINECTES SAPIDUS (BLUE CRAB MEAT)"
                            idcollapse="collap1"
                            fromText="From Venezuela, Colombia"
                            featureText="Ptrimer texto pequeño y asi" 
                            benefitText="This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer."

                            />
                            
                            <Product 
                            iconoProducto={blueCrabIcon}
                            idcollapse="collap2"
                            mensajeparrafo="segundo producto y descripcion" />
                            <Product />
                            <Product />

                        </div>

                    </div>

                </div>
            </div>
            </>
        )
    }
}

export default Products;

