import React from 'react';
import Cangrejo from '../../../img/phylosophy.png';
import Lampico from '../../../img/lamp.png';
import ReportIco from '../../../img/report.png';
import Mission from '../../../img/mission.png';


class Jumbotron extends React.Component {

  render() {

    return (

      <div className="contenedorphilo" >
        <div className="row">
          <div className="container col-12 text-center">
            <h1 className="titulos">Philosophy</h1>
          </div>
          <div className="col-6" style={{ paddingTop: "20px" }} >
            <img src={Cangrejo} alt="Cangrejo Azul" height="100%" width="110%" />
          </div>
          <div className="container col-6">
            <div className="row d-inline-block">
              <br/>
              <div className="float-left col-2 p-2 align-bottom">
                <img src={Lampico} alt="Bombillo rojo" height="65px" />
              </div>
              <div className="float-right col-10 p-2">
                <p className="pabout text-justify">
                  To be a leading company in the commercialization of fresh
                  and quality seafood, for the efficiency and technological
                  innovation of our products and for the differentiated image
                  of its brands, by selecting the best suppliers in this area, for
                  its distribution and massive consumption, through a careful
                  selection of the product and under the strictest quality
                  control.
                        </p>
              </div>
            </div>
            <div className="row d-inline-block">
              <div className="float-left col-2 p-2">
                <img src={ReportIco} alt="Bombillo rojo" height="65px" />
              </div>
              <div className="float-right col-10 p-2">
                <p className="pabout text-justify">

                  Our main work is the development of human resources
                  both professionally and personally, establishing Wellrtrained,
                  selfrdemanding Work teams, oriented to the service of our
                  clients, to results and quality, as a consequence of strategic
                  thinking and a learning attitude.
                        </p>
              </div>
            </div>
            <div className="row d-inline-block">
              <div className="float-left col-2 p-2">
                <img src={Mission} alt="Bombillo rojo" height="65px" />
              </div>
              <div className="float-right col-10 p-2">
                <p className="pabout text-justify ">
                  We envision ourselves as a modern, profitable company
                  present in the national market, actively participating in
                  external markets, developing our talents and skills in an
                  exceptional Work environment, motivated by continuously
                  seeking and satisfying the needs of customers and
                  consumers, promoting relationships of mutual
                  collaboration with our suppliers
                        </p>
              </div>
            </div>


          </div>
        </div>

      </div>

    )

  }

}

export default Jumbotron;
