import React, { useState } from 'react';
import milogo from '../../img/LOGOBLACK.png';
import { Link, Route, useLocation } from 'react-router-dom';

import SlideRoutes from 'react-slide-routes';

import Home from '../home/Home';
import Aboutus from '../aboutus/Aboutus';
import Contactus from '../contactus/Contactus';
import Phylosophy from '../phylosophy/Phylosophy';
//import Menu from './components/menu/Menu';
//import Footer from './components/footer/Footer';
//import ErrorPage from '../errorpages/ErrorPage';
import Products from '../products/Products';
import Callinectes from '../callinectes/Callinectes';
//import banner from '../../img/navmenu.jpg';




const Menu = () => {
    const [defaultBackground, setDefaultBackground] = useState(false);

    const location = useLocation();



    function handleDefaultBGI(defaultBackground) {
        if (defaultBackground) {
            setDefaultBackground(true);
            //document.getElementsByClassName('fondomenu').style.backgroundImage = "url('"+banner+"')";

        }
        else {
            setDefaultBackground(false);
            //document.getElementsByClassName('fondomenu').style.backgroundImage = "url('none')";
        }

        console.log(defaultBackground);
    }

    return (
        <>

            <div className={defaultBackground ? "fondomenuverdad" : "fondomenufalso"}    >
                <div className="mi-menu">
                    <ul className="nav nav-pills nav-fill">
                        <li className="nav-item">
                            <a className="navbar-brand" href="/">
                                <img src={milogo} alt="Logo VikingFood" width="100" height="100" />
                            </a>

                        </li>
                        <li className="nav-item">

                            <Link className="custom-link" to="/" onClick={() => handleDefaultBGI(false)}>HOME</Link>
                        </li>
                        <li className="nav-item">

                            <Link className="custom-link" to="/aboutus" onClick={() => handleDefaultBGI(true)} >ABOUT US</Link>

                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/phylosophy" onClick={() => handleDefaultBGI(true)}>PHILOSOPHY</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/products" onClick={() => handleDefaultBGI(true)}>PRODUCTS</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/contactus" onClick={() => handleDefaultBGI(true)}>CONTACT</Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div id="contenerdorslider" style={{ backgroundColor: defaultBackground ? 'white' : 'transparent' }}>
                <SlideRoutes location={location}   >
                    {/*aca van las paginas */}

                    <Route exact path='/' component={Home} />
                    <Route path='/aboutus' component={Aboutus} />
                    <Route path='/phylosophy' component={Phylosophy} />
                    <Route exact path='/products' component={Products} />
                    <Route exact path='/products/callinectes' component={Callinectes} />
                    <Route path='/contactus' component={Contactus} />



                </SlideRoutes>

            </div>

        </>
    )

}




export default Menu;