import React from 'react';

class Footer extends React.Component{
 
    render(){
        return(
            <div className="footerpie"  >
                <div  >
                    <div className="text-left font-weight-bold float-left" >&copy; {(new Date().getFullYear())} Viking Food   </div>
                    <div className="text-right float-right font-weight-bold" >Developed by Adversurge </div>
                </div>
            </div>
        )
    }
}

export default Footer;